.body {
  position: relative;
  background: rgba(0, 0, 0, .2);
  width: 60%;
  max-width: 768px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

@media (max-width: 920px) {
  .body {
    width: 100%;
  }

  .content {
    width: 100%;
    margin: 1rem auto !important;
  }
}

@media (max-width: 512px) {
  .footer {
    display: none !important;
  }
}

.x-center {
  margin: auto;
}

.title {
  color: white;
  text-align: center;
}

.main-title {
  padding-top: 1rem;
  padding-bottom: 0.5rem;
}

.content {
  position: relative;
  margin-top: 4rem;
  width: 80%;
  margin-left: 4rem;
  margin-right: 4rem;
  overflow-wrap: normal;
  margin-bottom: 2rem;
}

.right-content {
  text-align: right;
}

p,
content>li {
  margin-top: 1em;
  line-height: 1.5em;
}

.footer {
  position: relative;
  background: rgba(0, 71, 98, 0.2);
  height: 79px;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: auto;
}

.footer>h4 {
  margin-top: 1em;
  margin-left: 1em;
}

#menu {
  position: fixed;
  background: #000F1A;
  min-width: 200px;
  width: 20%;
  max-width: 16rem;
  height: 30rem;
  border-radius: 0 0 25px 0;
  border-right: 1px solid #004762;
  border-bottom: 1px solid #004762;
  transition: transform .3s cubic-bezier(0, .52, 0, 1);
}

#menu.false {
  transform: translate3d(-100vw, 0, 0);
}

#menu.true {
  transform: translate3d(0vw, 0, 0);
  overflow: hidden;
  z-index: 1;
}

nav {
  margin-top: 4rem;
  margin-left: 1rem;
}

nav>h3:first-child {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

nav>h3 {
  padding-top: 0.5rem;
  margin-bottom: 1rem;
}

.icons {
  float: left;
  margin-right: 1rem;
  margin-top: 0.25rem;
  filter: invert(16%) sepia(59%) saturate(2429%) hue-rotate(173deg) brightness(98%) contrast(102%);
}

.socials {
  position: absolute;
  width: 100%;
  height: 36px;
  bottom: 0;
  margin-bottom: 1rem;
  filter: invert(16%) sepia(59%) saturate(2429%) hue-rotate(173deg) brightness(98%) contrast(102%);
  display: flex;
  gap: 2rem;
  justify-content: center;
}

a {
  text-decoration: none;
}

.header-socials {
  filter: invert(16%) sepia(59%) saturate(2429%) hue-rotate(173deg) brightness(98%) contrast(102%);
  display: flex;
  margin-top: 1rem;
  gap: 2rem;
}

.header-socials>a {
  transition: 0.3s;
  border-radius: 1rem;
  padding: 0.25em;
}

.header-socials>a:hover {
  transform: translate(0px, 5px);
  box-shadow: 0px 0px 10px red;
}

section {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

h4 {
  margin-top: 1rem;
}

.left-half {
  width: 50%;
}

.right-half {
  width: 50%;
}

li {
  margin-top: 0.5rem;
}

.content>div>h4 {
  margin-top: 0rem;
}

.blog-thumbnail>img {
  padding: 0;
  margin: 0;
  display: block;
}

.blog-thumbnail {
  margin-top: 1rem;
  width: 100%;
  height: auto;
  display: flex;
}

.blog-thumbnail>h4 {
  margin-top: 0;
  margin-left: 1rem;
}

#blog>section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.project {
  transition: 0.3s;
  border-radius: 1rem;
  padding: 1rem;
}

.project:hover {
  transform: translate(-5px, 0px);
  box-shadow: 4px 8px 5px rgba(77, 150, 255, 0.5);
  transition: 0.2s;
}

.project:active {
  transform: translate(-2px, 3px);
  box-shadow: inset 1px 1px 5px rgba(0, 0, 0, .2);
}
